import Vue from 'vue'
import ElementUI from 'element-ui'
// import 'element-ui/lib/theme-chalk/index.css'
import '../theme/index.css'

import App from './App'
import router from './router'
import store from '@/store/store'
import axios from 'axios'
import videoPlayer from 'vue-video-player'
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')

Vue.config.productionTip = false
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
Vue.prototype.$axios=axios

Vue.use(ElementUI)
Vue.use(videoPlayer)

/* eslint-disable no-new */
new Vue({
  el: '#index',
  router,
  template: '<App/>',
  components: { App },
  store,
  beforeCreate: function() {
    axios.get('/api/nineteencongress/public/admin/article/getarticles')
    .then(function(response) {
      store.commit('initarticle',response.data.data);
      store.commit('newscountchange',response.data.data.length)
    }),
    axios.get('/api/nineteencongress/public/admin/video/getvideos')
    .then(function(response) {
      store.commit('initvideo',response.data.data);
      store.commit('videoscountchange',response.data.data.length)
    })
  }
})




