<template>
  <div id="nav">
    <el-row>
      <el-col :span="24" id="dp-container"><img src="./assets/bg.jpg" alt="pic" width="1400px"/></el-col>
    </el-row>
      <el-menu :default-active="this.$route.path" class="el-menu-demo" mode="horizontal" @select="handleselect($event)" background-color="#B40000" text-color="#ffffff">
        <el-menu-item class="indexlink" index="/index">首页</el-menu-item>
        <el-menu-item class="indexlink" index="/news/list">新闻</el-menu-item>
        <el-menu-item class="indexlink" index="/videos/list">视频</el-menu-item>
      </el-menu>
    <keep-alive>
    <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  methods: {
    handleselect: function(e) {
        this.$router.push({path:e})
      }
    }
  }
</script>
<style>
body {
  background: rgb(245,245,245);
}

.el-menu--horizontal > .el-menu-item.is-active {
  border: none;
}
  .indexlink {
    padding: 0 60px;
    font-size: 18px;
  }

  .el-menu-item.is-active {
    background: white !important;
  }

  #nav {
    text-align: center;
    width: 1400px;
    margin: 0 auto;
    background: white;
    min-height: 100vh;
    padding-bottom: 1rem;
  }

  .el-menu-demo {
    width: 1400px;
    margin: 0 auto;
    border: none;
  }

  #dp-container {
    height: 500px;
  }

  html,body {
    margin: 0;
    padding: 0;
  }

</style>


