<template>
  <div id="vlist">
    <p style="font-size: 25px;">视频</p>
    <div id="vcontainer">
      <el-row :gutter="18" class="row-1">
      <el-col class="col-1 shortcutcontainer" :span="6" v-for="(item,index) in this.$store.state.data.video.slice(this.$store.state.videopage*12-12,this.$store.state.videopage*12)" :key="index">
        <div @click='entervideos(item.id)'>
          <img v-bind:src="'http://120.79.199.124/nineteencongress/public'+item.pic_path.slice(1,)"  style="width:200px;height: 120px;" atl="视频图片">
          <p>{{item.title}}</p>
        </div>
      </el-col>
    </el-row>
    </div>
    <div class="block3">
      <el-pagination layout="prev, pager, next" :total="this.$store.state.videoscount" :page-size="12"  @current-change='handleCurrentChange'>
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'videos',
  methods: {
    handleCurrentChange:function(val) {
      this.$store.commit('videospagechange',val);
    },
    entervideos: function(e) {
      this.$router.push({path: '/videos/detail?id='+ e})
    }
  },
  mounted: function() {
    console.log(this.$store.state.data)
  }
}
</script>


<style>

  #vlist {
    width: 1200px;
    margin: 20px auto;
    min-height: 80vh;
  }
  .shortcutcontainer {
    cursor: pointer;
    display: inline-block;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .block3 {
    margin-top: 1rem;
  }
  #vcontainer {
    padding: 1rem 0;
    border: 1px solid rgb(221,221,221);
  }
</style>


