import Vue from 'vue'
import Router from 'vue-router'
import App from '@/App'
import News from '@/pages/News'
import Videos from '@/pages/Videos'
import index from '@/pages/index'
import newsdetail from '@/pages/newsdetail'
import videodetail from '@/pages/videodetail'


//2.调用vue-router
Vue.use(Router)
//3.创建vue-router实例
const router=new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/index',
      component: App
    },
    {
      path: '/index',
      name: '/index',
      component: index
    },
    {
      path: '/news/detail',
      name: '/news/detail',
      component: newsdetail
    },
    {
      path: '/videos/detail',
      name: '/videos/detail',
      component: videodetail
    },
    {
      path: '/news/list',
      name: '/news/list',
      component: News
    },
    {
      path: '/videos/list',
      name: '/videos/list',
      component: Videos
    },
    
  ]
});
export default router;



