<template>
  <div id="app">
    <el-container class="content">
      <carousel></carousel>
      <card></card>
    </el-container>
    <el-row class="content">
      <el-col :span="24">
        <router-link to="/videos/list">
          <img src="@/assets/red.jpg" alt="red.jpg" class="fill"><span class="fill" style="background-color: rgb(188,36,33); width: 600px;"></span>
          <p class="click">辅导员优秀宣传视频</p>
        </router-link>
      </el-col>
    </el-row>
    <shortcut></shortcut>
    <div></div>
  </div>
</template>

<script>
import Carousel from '@/components/Carousel';
import Card from '@/components/Card';
import Shortcut from '@/components/Shortcut';

export default {
  name: 'index',
  components: {
    'carousel': Carousel,
    'card': Card,
    'shortcut': Shortcut,
  }
}

</script>

<style>
  .content {
    width: 1000px;
    margin: 20px auto;
  }

  .fill {
    height: 40px;
    width: 400px;
    display: inline-block;
  }
  
  a {padding: 0}

  .click {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-60%);
    left: 250px;
    margin: 0;
    font-size: 30px;
    font-weight: bold;
    color: white;
  }

</style>



