<template>
  <div class="videodetail">
    <h1 style="font-size: 40px; margin-bottom: 3rem;">{{title}}</h1>
    <video-player  class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
    ></video-player>
  </div>
</template>
<script>
import axios from 'axios'
import qs from 'qs'
import { watch } from 'fs';

export default {
  name: 'BusImg',
  data () {
    return {
      playerOptions : {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: false, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: 'zh-CN',
        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [{
          type: "",
          src: ''
        }],
        poster: "", 
        notSupportedMessage: '此视频暂无法播放，请稍后再试', 
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true  
        }
      },
      title: ''
    }
  },
  created: function() {
    this.getData()
},
  methods: {
    getData: function() {
      var that = this
      var url = location.search;
      url = parseInt(url.substring(4,))
      let data = {"id": 16}
      axios.post('/api/nineteencongress/public/admin/video/getvideodetail',qs.stringify(data))
      .then(function(response) {
        console.log(response.data)
        var path = 'http://120.79.199.124/nineteencongress/public' + response.data.data.video_path
        that.playerOptions.sources[0].src = path
        that.title = response.data.data.title
      })
      console.log(this.playerOptions.sources[0].src)
      }
  },
  watch:{
    "$route":"getData"    //后面是methods中的一个方法
  } 
}
 
</script>

<style>
  .videodetail {
    margin-top: 20px;
    padding-bottom: 20%;
  }

  .video-player {
    width: 70%;
    margin: 0 auto;
  }
</style>


