<template>
  <el-card class="box-card">
    <div v-for="(item,index) in this.$store.state.data.article.slice(0,6)" :key="index" class="text">
      <div @click="enternews(item.id)">{{item.title}}</div>
    </div>
  </el-card>
</template>

<script>
  export default{
    methods: {
      enternews: function(e) {
      this.$router.push({path: '/news/detail?id='+ e})
    }
    }
  }
</script>

<style>
  .el-card {
    margin-left: 0px;
    height: 400px;
  }

  .el-card__body {
    padding: 0;
    height: 400px;
    display: flex;
    flex-direction: column;
  }

  .text {
    flex:1;
    font-size: 14px;
    text-align: left;
    margin:auto 20px;
    position: relative;
    box-sizing: border-box
  }

  .text>div {
    position: absolute;
    top: 50%;
    transform: translate(0,-50%);
    cursor: pointer;
  }
  .text>div::before {
    content: "\2022";     
    color: black;       
    font-size: 25px;      
    margin-right: .5rem
  }

  .box-card {
    width: 300px;
  }
</style>


