import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex);

const state = {
  newspage:1,
  videopage:1,
  newscount: 1,
  videoscount: 1,
  data:{
    'article':[],
    'video': []
  },
  conetent: ""
}

export default new Vuex.Store({
  state,
  mutations: {
    initarticle(state,response) {
      state.data.article = response;
    },
    initvideo(state,response) {
      state.data.video = response;
    },
    newscountchange(state,val) {
      state.newscount = val;
    },
    videoscountchange(state,val) {
      state.videoscount = val;
    },
    newspagechange(state,val) {
      state.newspage = val
    },
    videospagechange(state,val) {
      state.videopage = val
    }
  }
})


