<template>
  <div class="block">
    <el-carousel height="400px">
      <el-carousel-item v-for="(item,index) in this.path" :key="index">
        <img v-bind:src="'http://120.79.199.124/nineteencongress/public'+item.image" style="width: 100%;">
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<style>
  .block{
    margin: 0;
    width: 700px;
  }
  .el-carousel {
    padding: 0px 0px 0px 0px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
</style>

<script>
import axios from 'axios'
export default {
  data() {
    return  {
      path: []
    }
  },
  created: function() {
    this.getData()
  },
  methods: {
    getData: function() {
      var that = this;;
      axios.get('/api/nineteencongress/public/admin/pic/getpictures')
      .then(function(data) {
        that.path = data.data.data;
      })
    }
  }
}
</script>



