<template>
  <div id="news">
    <p style="font-size: 25px;">新闻</p>
    <div id="nlist">
      <li class="newsul" v-for="(item,index) in this.$store.state.data.article.slice(this.$store.state.newspage*10-10,this.$store.state.newspage*10)" :key="index" @click="enternews(item.id)">
        <span class="title">{{ item.title }}</span>
        <span class="time">{{ item.date }}</span>
      </li>
    </div>
      <div class="block2">
        <el-pagination layout="prev, pager, next" :total="this.$store.state.newscount" :page-size="10"  @current-change='handleCurrentChange'>
        </el-pagination>
      </div>
  </div>
</template>

<script>

export default {
  name: 'news',
  methods: {
    handleCurrentChange:function(val) {
      this.$store.commit('newspagechange',val);
    },
    enternews: function(e) {
      this.$router.push({path: '/news/detail?id='+ e})
    }
  },
  mounted: function() {
  }
}

</script>

<style>
  #nlist>li {
    list-style-type: none;
    
  }
  #nlist>li:before{
    content: "\2022";     
    color: red;       
    font-size: 20px;      
    margin-right: 1rem
  }
  #news {
    min-height: 80vh;
  }
  
  #nlist { 
    padding: 1rem 0;
    width: 80%;
    margin:0 auto;
    /* background-color: #FFFF99; */
    border: 1px solid rgb(221,221,221);
    margin-top: 20px;
    margin-bottom: 1rem;
  }

  .newsul {
    cursor: pointer;
  }

  #nlist>li {
    margin: 20px 5px 0px 0px;
    box-sizing: border-box;
    padding-bottom: 20px;
    border-bottom: 1px dotted rgb(221,221,221)
  }
  .title {
    display: inline-block;
    width: 70%;
    text-align:left;
  }
  .time {
    display: inline-block;
    width: 18%;
    font-size: 14px;
    color: rgb(150,150,150);
    text-align: right;
  }
</style>



