<template>
  <div id="container">
    <h1>{{title}}</h1>
    <h4>{{time}}</h4>
    <div class="newscontent" v-html="this.content"></div>
  </div>
</template>

<script>
import axios from 'axios'
import { defaultCoreCipherList } from 'constants';
import qs from 'qs'
import { resolve } from 'url';

export default {
  data() {
    return {
      content: "加载中...",
      title: '',
      time: ''
    }
  },
  created: function() {
    this.getData()
  },
  methods: {
    getData: function() {
      var that = this
      var url = location.search;
      url = parseInt(url.substring(4,))
      let data = {"id": url}
      axios.post('/api/nineteencongress/public/admin/article/getarticledetail',qs.stringify(data))
      .then(function(response) {
        that.content = response.data.data.content
        that.time = response.data.data.date
        that.title = response.data.data.title
      })
  }
  },
  watch:{
  "$route":"getData" 
}

}
</script>

<style>
  #container{
    width: 100%;
  }

  .newscontent {
   width: 629px;
   margin: 0 auto;
   margin-top: 20px; 
   text-align: left;
  }

  p {
    font-size: 14px;
  }
</style>



